<template>
  <div class="text-center">
    <v-dialog v-model="isShow" persistent max-width="600">
      <v-card>
        <v-card-title>
          <span class="text-h5">Editar Proximamente</span>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text>
          <v-row>
            <v-container>
              <v-row class="mt-0">
                <v-col cols="12" lg="12" sm="6" md="4">
                  <v-form ref="form">
                    <v-text-field
                      required
                      label="Titulo"
                      outlined
                      :rules="requiredParam"
                      v-model="dataForm.title"
                    ></v-text-field>
                    <v-file-input
                      counter
                      show-size
                      truncate-length="10"
                      accept="image/*"
                      label="Portada"
                      outlined
                      @change="(e) => (dataForm.portada = e)"
                    ></v-file-input>
                    <v-textarea
                      required
                      outlined
                      label="Descripcion"
                      :rules="requiredParam"
                      v-model="dataForm.description"
                    ></v-textarea>
                  </v-form>
                </v-col>
              </v-row> </v-container
          ></v-row>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="toggle"> Cerrar </v-btn>
          <v-btn color="blue darken-1" class="white--text" @click="saved()">
            Guardar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import axios from "../../services/axios";
export default {
  data: () => {
    return {
      isShow: true,
      valid: true,
      isRequest: false,
      requiredParam: [(v) => !!v || "Parametro es requerido"],
      dataForm: {
        title: null,
        description: null,
        portada: null,
      },
    };
  },
  methods: {
    saved() {
      if (this.$refs.form.validate()) {
        this.isRequest = true;
        let form = new FormData();
        console.log(this.item["id"]);
        form.append("id", this.item["id"]);
        form.append("title", this.dataForm.title);
        form.append("description", this.dataForm.description);
        if (this.dataForm.portada)
          form.append("portada", this.dataForm.portada);
        axios.put("/api/proximamente", form).then((res) => {
          this.isRequest = false;
          console.log(res);
          if (res.data["status"] == 200) {
            this.refresh();
            this.toggle();
          }
        });
      }
    },
  },
  created() {
    this.dataForm.title = this.item["title"];
    this.dataForm.description = this.item["description"];
  },
  props: ["item", "toggle", "refresh"],
};
</script>